import React from "react";
import "./listOfQuestions.scss";
import McqQuestionCard from "./McqQuestionCard";
import { secondsToMinutes } from "../../utils/utility";

export default function ListOfQuestionsNew(props) {
  return (
    <div className="list-card-main">
      <div className="list-card-heading">List Of Questions</div>

      {props.questions &&
        props.questions.map((item) => {
          if (item.skills && item.quesSec) {
            return (
              <div style={{ marginBottom: "10px" }}>
                <McqQuestionCard
                  skills={item.skills}
                  mcqText={item.questionText}
                  deleteHandler={() => {
                    props.deleteHandler(item);
                  }}
                  editHandler={() => {
                    props.editHandler(item);
                  }}
                  mcqOptions={item.mcqOptions}
                  time={`${secondsToMinutes(item.quesSec)}`}
                  coding={props.coding}
                 publish={item.isPublished}
                 assessment={true}
                />
              </div>
            );
          }
        })}
    </div>
  );
}
