import React from "react";
import "./createCoding.scss";
import ButtonNew from "../Controls/Button/Button";
import plus from "../../assets/images/Assessment/plus.png";
import ControlledRadio from "../Atoms/Radio";
import Autocomplete from "../Atoms/AutoComplete";
import { Chip } from "@material-ui/core";
import JoditEditor from "jodit-react";
import CodingQuestionModal from "./CodingQuestionModal";
import * as AssessmentActions from "../../store/Assessment/AssessmentAction";
import { bindActionCreators } from "redux";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { secondsToMinutes } from "../../utils/utility";
import { connect } from "react-redux";
import * as JobDataActions from "../../store/JobData/JobDataAction";
import CustomSelect from "../../components/Atoms/customSelect/customSelect";
import Checkbox from "@mui/material/Checkbox";

// import LoadingBar from "react-top-loading-bar";

let optionsValue = [];
let isOptionEmpty = false;
// let optionsValue2 = []
const config = {
  readonly: false,
  height: 180,
};

class CreateCoding extends React.Component {
  constructor(props) {
    super(props);
    const splitTime =
      this.props && this.props.rowData && this.props.rowData.suggestedDuration
        ? secondsToMinutes(this.props.rowData.suggestedDuration)
        : 0;
    this.state = {
      rowDataArrived: true,
      type: "",
      questionDetails: [
        {
          questionId: 1,
          questionText:
            (this.props && this.props.rowData && this.props.rowData.text) || "",
          quesMins: splitTime ? splitTime.split(":")[0] : 0,
          quesSec: splitTime ? splitTime.split(":")[1] : 0,
          skills:
            (this.props && this.props.rowData && this.props.rowData.skills) ||
            [],
          multipleInputVal: "",
          allowedLanguages:
            (this.props &&
              this.props.rowData &&
              this.props.rowData.allowedLanguages) ||
            [],
          autocomplete:
            (this.props &&
              this.props.rowData &&
              this.props.rowData.autocomplete) ||
            true,
        },
      ],
      radioValue:
        this.props && this.props.rowData && this.props.rowData.text
          ? "Create Question"
          : "",
      radioOptions: ["Create Question", "Select Question"],
      AddedIndexes: [],
      isShowSelectModal: false,
      multipleInputVal2: "",
      selectedQuestions: [],
      searchSkills: [],
      showOptions:
        this.props && this.props.rowData && this.props.rowData.text
          ? true
          : false,
      errorMsg: "",
      mainSkills:
        this.props && this.props.rowData && this.props.rowData.skills
          ? this.props.rowData.skills
          : [],
      allQuestions: this.props.allQuestions,
      stop: true,
      progress: 0,
      // allowedLanguages:[],
      languageOptions: [],
      // autocomplete:true
    };
  }

  componentDidMount() {
    this.props.JobDataAction.getSkillS();
    this.props.AssessmentAction.getAllLanguages();
    // setInterval(() => this.handleProgressChange(), 100);
  }
  // handleProgressChange = () => {
  //   if (this.state.progress < 90 && !this.state.stop) {
  //     this.setState({
  //       progress: this.state.progress + 15,
  //     });
  //   }
  // };

  componentDidUpdate(prevProps) {
    const prev = prevProps && prevProps.AssessmentReducer;
    const cur = this.props && this.props.AssessmentReducer;
    if (
      prev.isQuestionAdded !== cur.isQuestionAdded &&
      cur.isQuestionAdded &&
      cur.questionCreated &&
      cur.questionCreated.data &&
      cur.questionCreated.data.type == localStorage.getItem("coding") &&
      this.props.testId &&
      this.props.codingQuestions
    ) {
      cur.isQuestionAdded = false;
      let codingQuestions = this.props.codingQuestions;
      codingQuestions.push({
        id:
          cur.questionCreated &&
          cur.questionCreated.data &&
          cur.questionCreated.data.id,
        score: 0,
        duration:
          cur.questionCreated &&
          cur.questionCreated.data &&
          cur.questionCreated.data.suggestedDuration,
      });
      const body = {
        codingQuestions: codingQuestions,
      };

      this.props.AssessmentAction.updateTest({
        body: body,
        id: this.props.testId,
      });
      const fields = [
        {
          questionId: 1,
          questionText: "",
          quesMins: 0,
          quesSec: 0,
          skills: [],
          allowedLanguages: [],
          autocomplete: true,
        },
      ];
      this.setState({
        questionDetails: fields,
        radioValue: "",
        mainSkills: [],
      });

      this.setState({
        progress: 100,
      });

      setTimeout(() => {
        this.setState({ stop: true });
      }, 1000);
    }

    if (
      prev.isGetAllLanguages != cur.isGetAllLanguages &&
      cur.isGetAllLanguages
    ) {
      let languageOptions = [];

      if (
        cur.allLanguages &&
        cur.allLanguages.data &&
        cur.allLanguages.data.length
      ) {
        cur.allLanguages.data.map((item) => {
          languageOptions.push({
            value: item.code,
            label: item.name,
            compiler: item.compiler,
          });
        });
      }

      this.setState({
        languageOptions: languageOptions,
      });
    }

    if (
      prev.isGetQuestions !== cur.isGetQuestions &&
      this.state.radioValue == "Select Question"
    ) {
      cur.isGetQuestions = false;
      this.setState({ isShowSelectModal: true });
    }

    if (prev.isTestUpdated !== cur.isTestUpdated) {
      // cur.isTestUpdated = false;
      this.setState({ radioValue: "" });
      // this.props.AssessmentAction.getTestById({ id: this.props.testId });
    }

    if (
      prev.isQuestionUpdated !== cur.isQuestionUpdated &&
      cur.isQuestionUpdated
    ) {
      this.props.AssessmentAction.getTestById({ id: this.props.testId });
      this.setState({ rowDataArrived: true });
      let fields = [
        {
          questionId: 1,
          questionText: "",
          quesMins: 0,
          quesSec: 0,
          skills: [],
          multipleInputVal: "",
          autocomplete: true,
          allowedLanguages: [],
        },
      ];
      this.setState({
        questionDetails: fields,
        radioValue: "",
        mainSkills: [],
      });
    }
  }

  static getDerivedStateFromProps(nextProps, currentState) {
    if (
      nextProps &&
      nextProps.rowData &&
      nextProps.rowData.text &&
      nextProps.isEditAll
    ) {
      currentState.showOptions = true;
    }
    if (
      nextProps &&
      nextProps.rowData &&
      nextProps.rowData.text &&
      nextProps.isEdit &&
      currentState.rowDataArrived
    ) {
      currentState.rowDataArrived = false;
      let splitTime =
        Math.floor(nextProps.rowData.suggestedDuration / 60) +
        ":" +
        Math.floor(nextProps.rowData.suggestedDuration % 60);
      currentState.questionDetails[0].questionText = nextProps.rowData.text;
      currentState.questionDetails[0].quesSec = splitTime.split(":")[1];
      currentState.questionDetails[0].quesMins = splitTime.split(":")[0];
      currentState.questionDetails[0].skills = nextProps.rowData.skills;
      currentState.mainSkills = nextProps.rowData.skills;
      currentState.radioValue = "Create Question";
      currentState.showOptions = true;
      let allowedLanguages = [];
      if (
        nextProps.rowData.metadata &&
        nextProps.rowData.metadata.allowedLanguages
      ) {
        nextProps.rowData.metadata.allowedLanguages.map((item) => {
          allowedLanguages.push({
            value: item.code,
            label: item.name,
            compiler: item.compiler,
          });
        });
      }
      currentState.questionDetails[0].allowedLanguages = allowedLanguages;
      currentState.questionDetails[0].autocomplete =
        nextProps.rowData.metadata && nextProps.rowData.metadata.autocomplete;
    }
    return currentState;
  }

  clearFilter = async () => {
    await this.setState({
      searchSkills: [],
    });
  };

  handleChangeRadio = (e) => {
    this.setState({
      radioValue: e.target.value,
    });

    if (e.target.value == "Select Question") {
      const payload = {
        page: 1,
        limit: 10,
        type: localStorage.getItem("coding"),
      };
      this.props.AssessmentAction.getQuestions(payload);
    }
  };

  renderValues = (value, getTagProps) => {
    optionsValue = value;
    let newVals = [];
    value.map((val) => {
      let i = val.split(",");
      i.length === 1 ? newVals.push(i[0]) : i.map((el) => newVals.push(el));
    });
    return newVals.map((option, index) => (
      <Chip variant="outlined" label={option} {...getTagProps({ index })} />
    ));
  };

  renderValues2 = (value, getTagProps) => {
    if (this.state.searchSkills.length) {
      // optionsValue2 = value
      let newVals = [];
      value.map((val) => {
        let i = val.split(",");
        i.length === 1 ? newVals.push(i[0]) : i.map((el) => newVals.push(el));
      });

      return newVals.map((option, index) => (
        <Chip variant="outlined" label={option} {...getTagProps({ index })} />
      ));
    } else {
      // optionsValue2=[]
      return <div></div>;
    }
  };

  handleField = (e, name, index) => {
    let fields = this.state.questionDetails;
    const tempObject = { ...fields[index] };
    if (name == "questionText" || name == "allowedLanguages") {
      tempObject[name] = e;
    } else if (name == "autocomplete") {
      tempObject[name] = !tempObject.autocomplete;
    } else {
      tempObject[name] = e.target.value;
    }
    fields[index] = tempObject;
    this.setState({ questionDetails: fields, errorMsg: "" });
    // debugger
  };

  handleMutilpleSkills = (e, name, index) => {
    let fields = this.state.questionDetails;
    const tempObject = { ...fields[index] };
    tempObject[name] = e.target.value;
    tempObject["skills"] = optionsValue;
    fields[index] = tempObject;
    this.setState({ vehicleDetails: fields });
  };

  handleMutilpleSkills2 = (e) => {
    // this.setState({
    //   multipleInputVal2: e.target.value,
    // });
  };

  onChangeSkills = (e, value) => {
    this.setState({
      searchSkills: value,
    });
  };

  handleAddQuestionForm = () => {
    this.setState({
      showOptions: true,
    });
  };

  onChangeMainSkills = (event, value) => {
    this.setState({
      mainSkills: value,
    });
  };

  handleAddQuestion = (index) => {
    const { questionDetails } = this.state;
    let data = {};
    if (questionDetails[0].questionText === "") {
      data = {
        ...data,
        questionText: "Please enter question",
      };
    }

    if (
      parseInt(questionDetails[0].quesMins) > 59 ||
      parseInt(questionDetails[0].quesSec) > 59
    ) {
      data = {
        ...data,
        quesMins: "Please Enter valid time",
      };
    } else {
      if (
        !parseInt(questionDetails[0].quesMins) &&
        !parseInt(questionDetails[0].quesSec)
      ) {
        data = {
          ...data,
          quesMins: "Please Enter valid time",
        };
      }
    }
    if (questionDetails[0].skills.length === 0) {
      data = {
        ...data,
        skills: "Please enter skills",
      };
    }

    if (
      questionDetails[0].allowedLanguages &&
      questionDetails[0].allowedLanguages.length === 0
    ) {
      data = {
        ...data,
        allowedLanguages: "Please Select Languages",
      };
    }

    this.setState({
      errorMsg: data,
    });
    if (
      questionDetails[0].questionText !== "" &&
      (questionDetails[0].quesMins > 0 || questionDetails[0].quesSec > 0) &&
      questionDetails[0].skills.length > 0 &&
      questionDetails[0].allowedLanguages &&
      questionDetails[0].allowedLanguages.length > 0 &&
      !data.questionText &&
      !data.quesMins &&
      !data.skills &&
      !data.allowedLanguages
    ) {
      const { questionType } = this.props.AssessmentReducer;
      let allowedLanguages = [];
      questionDetails[0].allowedLanguages.map((item) => {
        allowedLanguages.push({
          code: item.value,
          name: item.label,
          compiler: item.compiler,
        });
      });
      const payload = {
        text: questionDetails[0].questionText,
        type: localStorage.getItem("coding")
          ? localStorage.getItem("coding")
          : "",
        skills: questionDetails[0].skills,
        metadata: {
          allowedLanguages: allowedLanguages,
          autocomplete: questionDetails[0].autocomplete,
        },
        suggestedDuration:
          Number(questionDetails[0].quesMins * 60) +
          Number(questionDetails[0].quesSec),
      };
      const id = (this.props.rowData && this.props.rowData.id) || "";
      if (this.props.isEdit) {
        this.props.AssessmentAction.updateQuestion(payload, id);
      } else {
        this.setState({
          stop: false,
          progress: 0,
        });
        this.props.AssessmentAction.createQuestion(payload);
      }
    }
  };

  handleSelection = (item) => {
    let allQuestions = this.state.allQuestions;
    let selectedQuestions = this.state.selectedQuestions;
    if (!selectedQuestions.length) {
      allQuestions.map((item2) => {
        if (item.id == item2.id) {
          item2.selected = true;
        }
      });

      this.setState({
        allQuestions: allQuestions,
      });

      allQuestions.map((item3) => {
        if (item3.selected == true && !selectedQuestions.includes(item3)) {
          selectedQuestions.push(item3);
        }
      });

      this.setState({
        selectedQuestions: selectedQuestions,
      });
    }
  };

  handleDeSelection = (item) => {
    let allQuestions = this.state.allQuestions;
    let selectedQuestions = this.state.selectedQuestions;
    allQuestions.map((item2) => {
      if (item.id == item2.id) {
        item2.selected = false;
      }
    });

    this.setState({
      allQuestions: allQuestions,
    });

    selectedQuestions = selectedQuestions.filter((item3) => {
      return item3 != item;
    });

    this.setState({
      selectedQuestions: selectedQuestions,
    });
  };

  questionDetailForm = () => {
    const { JobDataReducer } = this.props;
    let form =
      this.state.questionDetails &&
      this.state.questionDetails.map((questionDetail, i) => {
        return this.state.AddedIndexes.includes(i) ? null : (
          <div
            key={i}
            style={{
              marginBottom: "20px",
            }}
          >
            {this.props.isFromQuestionTab && (
              <div style={{ marginBottom: "10px" }}>
                <ArrowBackIcon
                  style={{ color: "#21745D", width: "25px", cursor: "pointer" }}
                  onClick={() => this.props.hideAddQuestion()}
                />
                <span style={{ fontWeight: "700", marginLeft: "10px" }}>
                  {this.props.isEdit ? "Update Question" : "Add Question"}
                </span>
              </div>
            )}
            <div className="coding-question-area">
              <div className="coding-editor">
                <JoditEditor
                  ref={this.setStateeditor}
                  value={
                    questionDetail.questionText
                      ? questionDetail.questionText
                      : ""
                  }
                  config={config}
                  onBlur={(newContent) =>
                    this.handleField(newContent, "questionText", i)
                  }
                  onChange={(newContent) =>
                    this.handleField(newContent, "questionText", i)
                  }
                />
              </div>
            </div>

            <div className="errorMsg">
              {questionDetail.questionText === "" &&
                this.state.errorMsg.questionText}
            </div>

            <div className="language-completion-area">
              <div className="language-options">
                <div className="options-select">
                  <CustomSelect
                    options={this.state.languageOptions}
                    selected={questionDetail.allowedLanguages || []}
                    placeholder="Allowed Languages"
                    handleChange={(newContent) => {
                      this.handleField(newContent, "allowedLanguages", i);
                    }}
                    clearAllSelected={() => this.clearLanguages()}
                  />
                </div>
                <div className="errorMsg">
                  {!(
                    questionDetail.allowedLanguages &&
                    questionDetail.allowedLanguages.length
                  ) && this.state.errorMsg.allowedLanguages}
                </div>
              </div>

              <div className="autocomplete-option">
                <div className="wrapper">
                  <div className="label-div">Auto Completion</div>
                  <div className="checkbox-div">
                    <Checkbox
                      sx={{
                        "&.Mui-checked": {
                          color: "#04724d",
                        },
                      }}
                      checked={questionDetail.autocomplete}
                      onChange={(newContent) => {
                        this.handleField(newContent, "autocomplete", i);
                      }}
                    />
                  </div>
                </div>
              </div>
            </div>

            <div className="coding-question-min-sec">
              <div className="coding-ques-min">
                <input
                  type="number"
                  name="quesMin"
                  min="0"
                  className="coding-quesMin"
                  onChange={(e) => {
                    this.handleField(e, "quesMins", i);
                  }}
                  placeholder="MM"
                  value={questionDetail.quesMins ? questionDetail.quesMins : ""}
                />
              </div>

              <div className="coding-ques-sec">
                <input
                  type="number"
                  name="quesSec"
                  min="0"
                  className="coding-quesSec"
                  onChange={(e) => {
                    this.handleField(e, "quesSec", i);
                  }}
                  placeholder="SS"
                  value={questionDetail.quesSec ? questionDetail.quesSec : ""}
                />
              </div>
            </div>
            <div className="errorMsg">{this.state.errorMsg.quesMins}</div>
            <div className="coding-ques-skills">
              <Autocomplete
                placeholder={
                  questionDetail.skills.length > 0 ? "" : "Add Skills"
                }
                multiple
                onChange={this.onChangeMainSkills}
                className="coding-multiSelectContainer coding-questionSkills2"
                onKeyDown={(e) =>
                  this.handleMutilpleSkills(e, "multipleInputVal", i)
                }
                renderTags={(value, getTagProps) =>
                  this.renderValues(value, getTagProps)
                }
                defaultValue={
                  questionDetail.skills
                    ? questionDetail.skills
                    : optionsValue
                    ? this.props.optionsValue
                    : []
                }
                options={
                  (JobDataReducer &&
                    JobDataReducer.skillData &&
                    JobDataReducer.skillData.data &&
                    JobDataReducer.skillData.data.map((x, i) => {
                      return {
                        value: i,
                        label: x,
                      };
                    })) ||
                  []
                }
              />
              <div className="errorMsg">
                {" "}
                {!this.state.mainSkills.length && this.state.errorMsg.skills}
              </div>
              <div className="coding-add-question-wrapper-2">
                <ButtonNew
                  className="coding-add-question-button-2"
                  onClick={() => {
                    this.handleAddQuestion(i);
                  }}
                >
                  {this.props.isEdit ? "update Question" : "Add Question"}
                </ButtonNew>
              </div>
            </div>
          </div>
        );
      });
    return form;
  };

  // handleInput = (e, item, questionDetail) => {
  //   questionDetail.options.map((item2) => {
  //     if (item2.id == item.id) {
  //       item2.text = e.target.value;
  //     }
  //   });
  //   let questionIndex = questionDetail.questionId;
  //   let fields = this.state.questionDetails;

  //   fields[questionIndex - 1] = questionDetail;

  //   this.setState({ questionDetails: fields });
  // };

  // handleDeleteItem = (item, questionDetail) => {
  //   if (questionDetail.options.length > 2) {
  //     let newOptions = questionDetail.options.filter((item2) => {
  //       return item2.id != item.id;
  //     });

  //     questionDetail.options = newOptions;
  //     let questionIndex = questionDetail.questionId;
  //     let fields = this.state.questionDetails;

  //     fields[questionIndex - 1] = questionDetail;

  //     this.setState({ questionDetails: fields });
  //   }
  // };

  // handleCheckbox = (item, questionDetail) => {
  //   questionDetail.options.map((item2) => {
  //     if (item2.text == item.text) {
  //       item2.correct = !item2.correct;
  //     }
  //   });

  //   let questionIndex = questionDetail.questionId;
  //   let fields = this.state.questionDetails;

  //   fields[questionIndex - 1] = questionDetail;

  //   this.setState({ questionDetails: fields });
  // };

  // handleAddOption = (questionDetail) => {
  //   if (questionDetail.options.length < 4) {
  //     let questionIndex = questionDetail.questionId;
  //     let newOption = {
  //       id: questionDetail.options.length + 1,
  //       text: "",
  //       correct: false,
  //     };
  //     questionDetail.options.push(newOption);

  //     let fields = this.state.questionDetails;

  //     fields[questionIndex - 1] = questionDetail;

  //     this.setState({ questionDetails: fields });
  //   }
  // };

  handleAllowedLanguages = (e) => {
    this.setState({ allowedLanguages: e });
  };

  render() {
    const { JobDataReducer } = this.props;

    let allQuestions = this.state.allQuestions;
    let allQuestions2 = [];
    if (this.state.searchSkills.length) {
      allQuestions.map((item) => {
        let containsAll = this.state.searchSkills.every((element) => {
          return item.skills.includes(element);
        });

        if (containsAll == true) {
          allQuestions2.push(item);
        }
      });
      allQuestions = allQuestions2;
    }
    return (
      <div className="coding-CreateCoding-Main">
        {/* {!this.state.stop ? (
        <LoadingBar
          color="#04724d"
          height={5}
          progress={this.state.progress}
          onLoaderFinished={() => this.setState({progress:0})}
        />
      ) : null} */}
        {!this.props.isFromQuestionTab && (
          <div className="coding-add-question-wrapper">
            <ButtonNew
              className="coding-add-question-button"
              startIcon={plus}
              onClick={this.handleAddQuestionForm}
            >
              Add Question
            </ButtonNew>
          </div>
        )}
        {this.state.showOptions && !this.props.isFromQuestionTab ? (
          <div className="coding-controlled-radio">
            <ControlledRadio
              radioOptions={this.state.radioOptions}
              radioValue={this.state.radioValue}
              handleChangeRadio={(e) => {
                this.handleChangeRadio(e);
              }}
            />
          </div>
        ) : null}
        {this.state.radioValue == "Create Question" ||
        this.props.isFromQuestionTab
          ? this.questionDetailForm()
          : null}

        {this.state.radioValue == "Select Question" ? (
          <CodingQuestionModal
            isShowSelectModal={this.state.isShowSelectModal}
            onCloseModal={() => {
              this.setState({ isShowSelectModal: false, radioValue: "" });
            }}
            handleMutilpleSkills2={this.handleMutilpleSkills2}
            skillData={
              JobDataReducer &&
              JobDataReducer.skillData &&
              JobDataReducer.skillData.data &&
              JobDataReducer.skillData.data
            }
            cancelSelect={this.cancelSelect}
            codingQuestions={this.props.codingQuestions}
            testId={this.props.testId}
            deleteHandler={this.deleteHandler}
            isViewOnly={this.props.isViewOnly}
          />
        ) : null}
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    AssessmentAction: bindActionCreators(AssessmentActions, dispatch),
    JobDataAction: bindActionCreators(JobDataActions, dispatch),
  };
};
const mapStateToProps = (state) => {
  return {
    AssessmentReducer: state.AssessmentReducer,
    JobDataReducer: state.JobDataReducer,
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(CreateCoding);
